//       // 画面幅1025px以上で適用
//       slidesPerView: 4,
//       spaceBetween: 20,
//     },
//   },
// })

document.querySelectorAll('.js-sliderProduct').forEach((slider) => {
  const sliderId = slider.getAttribute('data-slider-id')
  const slidesPerView = parseInt(slider.getAttribute('data-slides-per-view')) || 1
  const slidesPerViewSP = parseInt(slider.getAttribute('data-slides-per-view_sp')) || 3

  new Swiper(slider, {
    loop: true,
    slidesPerView: slidesPerViewSP,
    spaceBetween: 10,
    navigation: {
      nextEl: `.swiper-button-next[data-slider-id="${sliderId}"]`,
      prevEl: `.swiper-button-prev[data-slider-id="${sliderId}"]`,
    },
    breakpoints: {
      // ブレークポイント
      1025: {
        // 画面幅1025px以上で適用
        slidesPerView: slidesPerView,
        spaceBetween: 20,
      },
    },
  })
})

