const backToTop = document.querySelector('.back2top')
backToTop.addEventListener('click', () => {
  window.scrollTo({
    top: 0,
    behavior: 'smooth',
  })
})

if (document.getElementsByClassName('js-product-slider')[0]) {
  //商品詳細ページのスライダー
  let productSliderThumbs = new Swiper('.js-product-slider-nav', {
    slidesPerView: 4,
    spaceBetween: 6,
    breakpoints: {
      // ブレークポイント
      769: {
        // 画面幅769px以上で適用
        slidesPerView: 5,
        spaceBetween: 4,
      },
    },
  })

  let productSlider = new Swiper('.js-product-slider', {
    loop: true,
    thumbs: {
      //サムネイル
      swiper: productSliderThumbs,
    },
  })
}
